<template>
  <layout-default>
    <div class="page-content">
      <div class="container-fluid">

          <!-- start page title -->
          <div class="row">
              <div class="col-12">
                  <div class="page-title-box d-flex align-items-center justify-content-between">
                      <h4 class="mb-0">جدول المنتجات</h4>
                      <div class="page-title-right">
                          <button type="button" @click="$refs.FormModal.create()" class="btn btn-success waves-effect waves-light">
                              + إضافة منتج جديد
                          </button>
                          <!--
                          <button type="button" @click="print()" class="btn btn-success waves-effect waves-light">
                              + إضافة منتج جديد
                          </button>
                          -->
                          <form-modal ref="FormModal" :fetchData="fetchData"/>
                      </div>

                  </div>
              </div>
          </div>
          <!-- end page title -->
          
          <div class="row">
              <div class="col-12">
                  <div class="card">
                      <div v-if="products&&products.data.length" class="card-body">
                      <div class="card-title mb-5">
                            <div class="form-inline float-md-end">
                                <div class="search-box ms-2">
                                    <div class="position-relative">
                                        <input type="text" v-model="search" @change="fetchData" class="form-control bg-light rounded" placeholder="Search...">
                                        <i class="mdi mdi-magnify search-icon"></i>
                                    </div>
                                </div>
                            </div>
                          </div>
                          <table id="datatable" class="table table-bordered dt-responsive nowrap" style="border-collapse: collapse; border-spacing: 0; width: 100%;">
                              <thead>
                              <tr>
                                  <th >الأسم</th>
                                  <th >الصنف</th>
                                  <th >السعر</th>
                                  <th >الخصم</th>
                                  <th >خيارات</th>
                              </tr>
                              </thead>
                              <tbody>
                              <tr v-for="(product,index) in products.data">
                                  <td>{{product.name}}</td>
                                  <td>{{appendParentName(product.category)}}</td> 
                                  <td v-if="product.discount>0"><del class="me-2">{{moneyFormat(product.price)}} L.E </del>
                                  {{moneyFormat(product.price - (product.price * product.discount / 100))}}
                                  </td>
                                  <td v-else>{{moneyFormat(product.price)}} L.E</td>
                                  <td>{{moneyFormat(product.discount)}} %</td>
                                  <td>
                                    <div class="btn-group">
                                        <button  type="button" class="btn btn-light dropdown-toggle" data-bs-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                          المزيد <i class="mdi mdi-chevron-down"></i>
                                        </button>
                                        <div class="dropdown-menu" style="min-width: 120px;">
                                            <!--
                                            <router-link :to="{ name: 'client', params: { client_id: client.id } }" tag="a" class="dropdown-item">
                                                <i class="far fa-eye"></i> Show
                                            </router-link>
                                            -->
                                            <a @click="$refs.FormModal.edit(product)"  class="dropdown-item dropdownItem" href="javascript:;">
                                              <i class="far fa-edit"></i> تعديل
                                            </a>
                                            <a @click="$refs.FormModal.destroy(product.id)"  class="dropdown-item dropdownItem" href="javascript:;">
                                              <i class="far fa-trash-alt"></i> حذف
                                            </a>
                                        </div>
                                    </div>
                                  </td>
                              </tr>
                              </tbody>
                          </table>
                          <pagination-nav v-if="products.last_page>1" :pageSize="5" :currentPage="2" :GoToPage="GoToPage" />
                      </div>
                      <div v-else class="card-body">
                          <beat-loader v-if="loader" :loading="loader" color="#5578eb"></beat-loader>
                          <p v-else class="card-title-desc" style="text-align: center;">
                            لا يوجد منتجات تمت إضافتها
                          </p>
                      </div>
                  </div>
              </div> <!-- end col -->
          </div> <!-- end row -->
      </div> <!-- container-fluid -->
    </div>
  </layout-default> 
</template>  


<script>
import LayoutDefault from '../../layouts/App';
import PaginationNav from '../../components/pagination';
import FormModal from '../../components/modals/product/add';
import axios from 'axios';

export default {
  components: {LayoutDefault,PaginationNav,FormModal},
  data() {
      return {products:null,loader:false,search:null}
    },  
  created() {
    this.fetchData();
  },
  methods: {
    fetchData(num=1) {
      this.products=null;
      this.loader=true;
      let data = {params: {page:num}};
      axios.get('/api/products',data).then(({data}) => {
          this.products=data.success.products;
          if(!data.success.products.data.length&&num!=1){
              this.fetchData(1)
          }           
          this.loader=false;
      });  
    },
  },
}
</script>